// action
const SET_LANGUAGE = 'SET_LANGUAGE';

// action
export function setLanguage(language) {
  return {
    type: SET_LANGUAGE,
    payload: language,
  };
}

// 
const initialState = {
  language: 'zh', // 
};

// reducer
export function languageReducer(state = initialState, action) {
  switch (action.type) {
    case SET_LANGUAGE:
      return {
        language: action.payload,
      };
    default:
      return state;
  }
}